import { createStore, combineReducers } from "redux";

import { persistStore, persistReducer } from "redux-persist";
import storage from "redux-persist/lib/storage";

import { db } from "../firebase";

const initialState = {
  uid: "ansodnasodnonad",
  displayName: "NOME",
  email: "EMAIL",
  photoURL:
    "https://skylab.rocketseat.com.br/static/adc3e83e17d37cfa9fc2dc69f1f284f9.png",
  lastSignInTime: "sss",
  devices: null,
  dashboard: [{}]
};
const reducer = (state = initialState, action) => {
  if (action.type === "SET_USER") {
    return {
      ...state,
      displayName: action.displayName,
      email: action.email,
      uid: action.uid,
      lastSignInTime: action.lastSignInTime,
      photoURL: action.photoURL
    };
  }
  if (action.type === "SET_DEVICE") {
    return {
      ...state,
      devices: action.devices
    };
  }
  if (action.type === "SET_DASHBOARD") {
    return {
      ...state,
      dashboard: action.dashboard
    };
  }

  return state;
};

const persisConfig = {
  key: "farmly-root",
  storage
};
const persistedReducer = persistReducer(persisConfig, reducer);
const store = createStore(persistedReducer);
const persistor = persistStore(store);

export { store, persistor };
