import React, { Component } from "react";
import HomeStyles from "./style.module.css";
import { Link } from "react-router-dom";
import logo from "../../assets/logo.svg";
import { connect } from "react-redux";
import { compose } from "redux";
import { withRouter } from "react-router";

function Header(props) {
  return (
    <header>
      <div className={HomeStyles.cabecalho}>
        <div className={HomeStyles.logo}>
          <Link to="/">
            <img src={logo} />
          </Link>

          {props.location.pathname !== "/" && (
            <div
              className={HomeStyles.seta}
              onClick={() => props.history.goBack()}
            >
              <svg
                stroke="currentColor"
                fill="currentColor"
                strokeWidth="0"
                viewBox="0 0 448 512"
                height="1em"
                width="1em"
                xmlns="http://www.w3.org/2000/svg"
              >
                <path d="M257.5 445.1l-22.2 22.2c-9.4 9.4-24.6 9.4-33.9 0L7 273c-9.4-9.4-9.4-24.6 0-33.9L201.4 44.7c9.4-9.4 24.6-9.4 33.9 0l22.2 22.2c9.5 9.5 9.3 25-.4 34.3L136.6 216H424c13.3 0 24 10.7 24 24v32c0 13.3-10.7 24-24 24H136.6l120.5 114.8c9.8 9.3 10 24.8.4 34.3z"></path>
              </svg>
              Voltar
            </div>
          )}
        </div>

        <div className={HomeStyles.conta}>
          <Link to="/account">
            <div className={HomeStyles.a}>
              <div className={HomeStyles.conta_info}>
                <span>{props.name}</span>
                <span>{props.email}</span>
              </div>
              <img src={props.photoURL} alt="Profile" />
            </div>
          </Link>
        </div>
      </div>
    </header>
  );
}

const mapStateToProps = state => {
  return {
    name: state.displayName,
    email: state.email,
    photoURL: state.photoURL,

    device: state.devices
  };
};
export default compose(withRouter, connect(mapStateToProps))(Header);
