import React, { Component } from "react";
import HomeStyles from "./style.module.css";
import Solution from "../../../assets/solution_3.svg";
import Premium from "./../../../assets/premium.svg";

import { connect } from "react-redux";
import { Link } from "react-router-dom";
import * as funcoes from "../../../functions/setFunc";
class Box extends Component {
  componentDidMount() {
    console.log(this.props);
  }
  teste = () => {
    funcoes.setDashboard(this.props.uid, this.props.mac);
  };
  render() {
    const { item, index, status, versao } = this.props;

    return (
      <Link to="/dashboard">
        <a
          {...(status
            ? 'style={{ cursor: "not-allowed" }}'
            : 'style={{ cursor: "pointer" }}')}
          key={item + index}
          onClick={this.teste}
        >
          <div className={HomeStyles.adicionar}>
            <div id={HomeStyles.adicionar_dentro}>
              <span>0{index + 1}</span>
              <section>
                {versao == "Solution" && (
                  <img src={Solution} alt="Starter Cursos Gratuitos" />
                )}
                {versao == "Premium" && (
                  <img src={Premium} alt="Starter Cursos Gratuitos" />
                )}
              </section>
              {status ? (
                <p>
                  <strong>{item ? item : "GENERICAO"}</strong> está
                  <em style={{ color: "green" }}> online</em> e disponível para
                  modificações.
                </p>
              ) : (
                <p>
                  <strong>{item ? item : "GENERICAO"}</strong> está
                  <em style={{ color: "red" }}> offline</em> e indisponível para
                  alterações.
                </p>
              )}
            </div>
          </div>
        </a>
      </Link>
    );
  }
}
const mapStateToProps = (state) => {
  return {
    uid: state.uid,
  };
};
export default connect(mapStateToProps)(Box);
