import React, { Component } from "react";
import HomeStyles from "./style.module.css";
import Box from "../../components/Home/Box";
import { db } from "../../services/firebase";
import { connect } from "react-redux";
import Header from "../../components/Header";
import * as funcoes from "../../functions/setFunc";

class Home extends Component {
  constructor() {
    super();
  }
  componentWillMount() {
    console.log(this.props);
  }
  componentDidMount() {
    db.ref(`users/${this.props.uid}/Home/Device/`).on("value", (snapshot) => {
      funcoes.setDevices(this.props.uid);
    });
  }
  render() {
    const device = this.props.devices;

    return (
      <div>
        <div className={HomeStyles.outradiv}>
          <Header />
          <div className={HomeStyles.conteudo}>
            <div className={HomeStyles.conteudo_item} id={HomeStyles.outro}>
              {device === null ? (
                <span style={{ alignSelf: "center", justifySelf: "center" }}>
                  Vincule seu farmbot ao seu email pelo App para configurar sua
                  funcoes.
                </span>
              ) : (
                device.map((item, index) => {
                  return (
                    <Box
                      key={item.Info.Name + index}
                      mac={item.Info.Mac}
                      item={item.Info.Name}
                      index={index}
                      status={item.Info.Status}
                      versao={item.Info.Type}
                    />
                  );
                })
              )}
            </div>
          </div>
        </div>
      </div>
    );
  }
}

const mapStateToProps = (state) => {
  return {
    uid: state.uid,
    email: state.email,
    devices: state.devices,
  };
};
export default connect(mapStateToProps)(Home);
