import React, { Component } from "react";
import GoogleMapReact from "google-map-react";
import { Container } from "./style";
import PropTypes from "prop-types";
import config from "./config";
const Device = ({ farmName, status }) => (
  <>
    <div className="pin">
      <div className="caixa">
        <p>
          <img
            src="https://api.adorable.io/avatars/40/abott@adorable.png"
            alt="user"
            width="42"
            height="42"
            align="middle"
          />
          {farmName}
        </p>
        <span>{status}</span>
      </div>
    </div>

    <div className="base" />
  </>
);

class Map extends Component {
  /*TO DO 
  -retirar a logo do google 
 */
  render() {
    return (
      <Container>
        <GoogleMapReact
          bootstrapURLKeys={{ key: "AIzaSyA1V_i_2diSBMHlrgJNqx6QWP3tNKZ2rGs" }}
          defaultCenter={this.props.center}
          defaultZoom={this.props.zoom}
          options={this.props.options}
          mapTypeControl="false"
          styles={this.props.styles}
        >
          <Device
            lat={this.props.device1[0]}
            lng={this.props.device1[1]}
            farmName={this.props.farmName1}
            status={this.props.status1}
          />
          <Device
            lat={this.props.device2[0]}
            lng={this.props.device2[1]}
            farmName={this.props.farmName2}
            status={this.props.status2}
          />
        </GoogleMapReact>
      </Container>
    );
  }
}
Map.defaultProps = {
  device1: PropTypes.array.isRequired,
  device2: PropTypes.array.isRequired,
  farmName1: PropTypes.string.isRequired,
  farmName2: PropTypes.string.isRequired,
  status1: PropTypes.string.isRequired,
  status2: PropTypes.string.isRequired,

  center: {
    lat: -15.823694,
    lng: -47.733806
  },
  options: {
    disableDefaultUI: true,
    mapTypeId: "roadmap",
    styles: config
  },
  scrollwheel: false,
  zoom: 15
};

export default Map;
