import React, { Component } from "react";

import { Progress } from "react-sweet-progress";
import "react-sweet-progress/lib/style.css";
import { Wifi, Info, Help, Circle, Device } from "./style.js";

export default class BoxInfo extends Component {
  render() {
    return (
      <>
        <Progress
          style={{ width: 60, color: "#fff", margin: 10 }}
          strokeWidth={100}
          status="error"
          theme={{
            error: {
              symbol: (
                <span style={{ color: "white", marginLeft: -15, fontSize: 12 }}>
                  {Math.min(Math.max(2 * (this.props.Battery + 100), 0), 100)}%
                </span>
              ),
              color: "#15e17e",
              trailColor: "#0d6b3f",
            },
          }}
          percent={this.props.Battery}
        />
        <Info>
          <Circle
            onClick={() =>
              alert(
                `Nome: ${
                  this.props.Name ? this.props.Name : "GENERICAO"
                }\nVersão: ${this.props.Type}\nStatus: ${
                  this.props.Status ? "Online" : "Offline"
                }`
              )
            }
          >
            <Device />
          </Circle>
          <Circle onClick={() => alert("Voce precisa de ajuda?")} help>
            <Help />
          </Circle>
          <Circle
            onClick={() =>
              alert(
                `IP Address: ${this.props.IP}\nMAC Address: ${this.props.Mac}\nSSID: ${this.props.Wifi}`
              )
            }
          >
            <Wifi alt={this.props.Wifi} title={this.props.Wifi} />
          </Circle>
        </Info>
      </>
    );
  }
}
