import React, { Component } from "react";
import Header from "../../components/Header";
import { connect } from "react-redux";
import { WidthProvider, Responsive } from "react-grid-layout";
import { FullPage, Light, Help, Circle } from "./style.js";
import { db } from "../../services/firebase";
import BoxInfo from "../../components/Dashboard/Box";
import Map from "../../components/Dashboard/Map";
import { MdDragHandle } from "react-icons/md";
import * as funcoes from "../../functions/setFunc";
const ResponsiveReactGridLayout = WidthProvider(Responsive);
const originalLayouts = getFromLS("layouts") || {};

class Dashboard extends Component {
  constructor(props) {
    super(props);
    this.state = {
      layouts: JSON.parse(JSON.stringify(originalLayouts)),
    };
  }
  componentDidMount() {
    db.ref(`users/${this.props.uid}/Home/Device/`).on("value", (snapshot) => {
      funcoes.setDashboard(this.props.uid, this.props.dashboard.Info.Mac);
    });
  }
  static get defaultProps() {
    return {
      className: "layout",
      cols: { lg: 12, md: 10, sm: 6, xs: 4, xxs: 2 },
      rowHeight: 30,
    };
  }

  saveToLS(key, value) {
    if (global.localStorage) {
      global.localStorage.setItem(
        `rgl-8`,
        JSON.stringify({
          [key]: value,
        })
      );
    }
  }
  resetLayout() {
    this.setState({ layouts: {} });
  }

  onLayoutChange(layout, layouts) {
    this.saveToLS("layouts", layouts);
    this.setState({ layouts });
  }
  handleClick = (index, state) => {
    let rele = `users/${this.props.uid}/Home/Device/${this.props.dashboard.Info.Mac}/Output/${index}/relay`;
    db.ref(rele).set(!state);
  };

  render() {
    const Output = Object.keys(this.props.dashboard.Output).map(
      (key) => this.props.dashboard.Output[key]
    );

    const {
      Battery,
      Mac,
      Name,
      Status,
      Type,
      Wifi,
      IP,
    } = this.props.dashboard.Info;
    return (
      <FullPage>
        <Header />

        <div>
          <ResponsiveReactGridLayout
            draggableHandle=".drag"
            className="layout"
            style={{ color: "white" }}
            cols={{ lg: 12, md: 10, sm: 6, xs: 4, xxs: 2 }}
            rowHeight={30}
            layouts={this.state.layouts}
            onLayoutChange={(layout, layouts) =>
              this.onLayoutChange(layout, layouts)
            }
          >
            {Output.map((state, index) => {
              return (
                <div
                  className="relay"
                  key={index}
                  data-grid={{
                    w: 1,
                    h: 2,
                    x: index + 3,
                    y: 1,
                    minW: 1,
                    maxW: 1,
                    minH: 2,
                    maxH: 2,
                  }}
                >
                  <div className="drag">
                    <MdDragHandle />
                  </div>
                  <div className="relay_content">
                    <Circle
                      ison={state.current ? 1 : 0}
                      onClick={() => this.handleClick(index, state.relay)}
                    >
                      <Light ison={state ? 1 : 0} size="2em" />
                    </Circle>
                    <span>{state.Name}</span>
                  </div>
                </div>
              );
            })}
            <div
              className="info"
              key="info"
              data-grid={{
                w: 2.5,
                h: 4,
                x: 1,
                y: 3,
                minW: 2.5,
                maxW: 3,
                minH: 4,
                maxH: 4,
              }}
            >
              <div className="drag">
                <MdDragHandle />
              </div>
              <BoxInfo {...this.props.dashboard.Info} />
            </div>
            <div
              className="map"
              key="map"
              data-grid={{
                w: 4,
                h: 9,
                x: 1,
                y: 3,
                minW: 1,
                maxW: 4,
                minH: 9,
                maxH: 9,
              }}
            >
              <div className="drag">
                <MdDragHandle />
              </div>
              <Map
                device1={[-15.823694, -47.733806]}
                device2={[-15.81, -42.202]}
                farmName1="Chacara185A"
                farmName2="Outra Chacara"
                status1="online"
                status2="offline"
              />
            </div>
          </ResponsiveReactGridLayout>
        </div>
      </FullPage>
    );
  }
}
function getFromLS(key) {
  let ls = {};
  if (global.localStorage) {
    try {
      ls = JSON.parse(global.localStorage.getItem("rgl-8")) || {};
    } catch (e) {
      /*Ignore*/
    }
  }
  return ls[key];
}

const mapStateToProps = (state) => {
  return {
    name: state.displayName,
    email: state.email,
    uid: state.uid,
    photoURL: state.photoURL,
    dashboard: state.dashboard,
  };
};
export default connect(mapStateToProps)(Dashboard);
