import React, { Component, Fragment } from "react";

import { NotificacaoContext } from "../../contexts/NotificacaoContext";
import { Redirect, Link, Route } from "react-router-dom";
import { MdMail } from "react-icons/md";
import { FaLock } from "react-icons/fa";
import { Button } from "react-bootstrap";

import LoginStyle from "../Login/login.module.css";

import withFirebaseAuth from "react-auth-firebase";
import firebase, { db } from "../../services/firebase";

import * as funcoes from "../../functions/setFunc";

class Login extends Component {
  constructor() {
    super();
    this.state = {
      email: "",
      password: "",
      msgerror: ""
    };
  }
  componentWillMount() {
    window.prompt("Digite sua chave");
  }
  cadastrar = () => {
    this.props.signUpWithEmail(this.state.email, this.state.password);
    if (this.props.error) {
      alert(this.props.error.message);
    }
  };
  render() {
    const {
      signInWithEmail,
      signUpWithEmail,
      signInWithGoogle,
      signInWithFacebook,
      user,
      error,
      signOut
    } = this.props;
    const { email, password } = this.state;
    if (user) {
      funcoes.setUser(user);
      localStorage.setItem("TOKEN", user.uid);
      //this.context.setMsg("Bem-Vindo a plataforma!");
      if (funcoes.setUser(user)) {
        return <Redirect to="/" signOut={signOut} />;
      } else {
        return <Redirect to="/account" />;
      }
    }

    return (
      <Fragment>
        <div className="nada">
          <div className={LoginStyle.all}>
            <div className={LoginStyle.box}>
              <img
                id={LoginStyle.icone}
                src="https://skylab.rocketseat.com.br/static/c725bd03cf247dd67aede03fb401e719.svg"
              />
              <form>
                <label htmlFor="email" id={LoginStyle.inpt1}>
                  <span id={LoginStyle.iptico}>
                    <MdMail size="1.1em" color="#7C62D4" />
                  </span>
                  <input
                    type="text"
                    placeholder="E-mail"
                    id={LoginStyle.email}
                    name="email"
                    aria-label="email"
                    autoComplete="off"
                    autoFocus
                    onChange={e => this.setState({ email: e.target.value })}
                  />
                </label>
                <label htmlFor="senha" id={LoginStyle.inpt2}>
                  <span id={LoginStyle.iptico}>
                    <FaLock size="1em" color="#7C62D4" />
                  </span>
                  <input
                    type="password"
                    placeholder="Senha"
                    id={LoginStyle.senha}
                    name="senha"
                    aria-label="senha"
                    onChange={e => this.setState({ password: e.target.value })}
                  />
                </label>

                {this.state.password.length < 6 ? (
                  <Button
                    id={LoginStyle.entrar}
                    style={{
                      cursor: "not-allowed",
                      background: "#41366b",
                      color: "#8d86a3"
                    }}
                    size="lg"
                    block
                    disabled
                  >
                    Cadastrar
                  </Button>
                ) : (
                  <Button
                    id={LoginStyle.entrar}
                    size="lg"
                    block
                    onClick={this.cadastrar}
                  >
                    Cadastrar
                  </Button>
                )}

                <Link to="/Login">
                  <p className={LoginStyle.text_center}>
                    Já possui uma conta?
                    <a id={LoginStyle.colorido}>Entre</a>
                  </p>
                </Link>
              </form>
            </div>
          </div>
        </div>
      </Fragment>
    );
  }
}

const authConfig = {
  email: {
    verifyOnSignup: true, // Sends verification email to user upon sign up
    saveUserInDatabase: true // Saves user in database at /users ref
  },
  google: {
    redirect: false, // Opens a pop up by default
    returnAccessToken: true, // Returns an access token as googleAccessToken prop
    saveUserInDatabase: true // Saves user in database at /users ref
  },
  facebook: {
    // redirect: true, // Opens a pop up by default
    returnAccessToken: true, // Returns an access token as googleAccessToken prop
    saveUserInDatabase: true // Saves user in database at /users ref
  },
  github: {
    // redirect: true,
    returnAccessToken: true,
    saveUserInDatabase: true
  },
  twitter: {
    // redirect: true,
    returnAccessToken: true,
    returnSecret: true,
    saveUserInDatabase: true
  }
};
export default withFirebaseAuth(Login, firebase, authConfig);
