import { store } from "../services/redux/store";
import { db } from "../services/firebase";

export const setUser = (user) => {
  const {
    displayName,
    email,
    metadata: { lastSignInTime },
    uid,
    photoURL,
  } = user;
  setDevices(uid);
  if (displayName === null) {
    store.dispatch({
      type: "SET_USER",
      displayName: "Nome",
      email: email,
      lastSignInTime: lastSignInTime,
      uid: uid,
      photoURL:
        "https://skylab.rocketseat.com.br/static/adc3e83e17d37cfa9fc2dc69f1f284f9.png",
    });
    return false;
  }

  store.dispatch({
    type: "SET_USER",
    displayName: displayName,
    email: email,
    lastSignInTime: lastSignInTime,
    uid: uid,
    photoURL: photoURL,
  });
  return true;
};

export const setDevices = (user) => {
  db.ref(`users/${user}`)
    .child("Home")
    .orderByChild("Device")
    .once("value", (snapshot) => {
      if (snapshot.exists()) {
        //Se existe algum dispositivo registrado
        db.ref(`users/${user}/Home/Device`).on("value", function (snapshot) {
          let valor = snapshot.val();
          let devices = Object.keys(valor).map((key) => valor[key]);
          store.dispatch({
            type: "SET_DEVICE",
            devices: devices,
          });
        });
      } else {
        store.dispatch({
          type: "SET_DEVICE",
          devices: null,
        });
      }
    });
};

export const setDashboard = (user, device) => {
  db.ref(`users/${user}/Home/Device/${device}`).on("value", function (
    snapshot
  ) {
    let valor = snapshot.val();
    let dashboard = Object.keys(valor).map((key) => valor[key]);
    store.dispatch({
      type: "SET_DASHBOARD",
      dashboard: valor,
    });
  });
};
